html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: "Raleway", sans-serif;
    color: #202020;
}

img {
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    transition: all .3s;
}


main {
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    z-index: 10;
    min-width: 320px;
    max-width: 100%;
    padding: 0 15px;
    box-sizing: border-box;
    margin: 0 auto 0 374px;
    box-sizing: border-box;
}

aside {
    position: fixed;
    min-height: 100vh;
    background: #202020;
    left: 0;
    top: 0;
    width: 359px;
    display: flex;
    flex-direction: column;
    border-right: 15px solid #4F4F4F;
    box-shadow: 15px 0 0 #767676;
    text-align: center;
    padding: 57px 49px 0 82px;
    z-index: 20;

    .footer {
        margin-top: auto;

    }

}

@media only screen and (max-width : 386px) {
    aside {
        width: 100%;
        border: none;

    }
}

.visible {
    display: block;
    box-shadow: 15px 0 0 #767676, 15px 0 30px #4f4f4f;
}

@media only screen and (max-width : 1200px) {
    main {
        margin: 0 auto 0 auto;
        padding: 0 0;
    }
}

@media only screen and (max-width : 1200px) {
    aside {
        display: none;
    }


}

@media only screen and (max-width : 576px) {
    aside {
        padding: 17px 17px 0 17px;
    }
}
